import "core-js/modules/es.array.splice.js";
import Form from '@/components/Notes/Form.vue';
import List from '@/components/Notes/List.vue';
export default {
  components: {
    Form: Form,
    List: List
  },
  data: function data() {
    return {
      notes: [{
        title: 'анжуманя',
        tags: ['спорт']
      }, {
        title: 'прес качат',
        tags: ['спорт']
      }, {
        title: 'бегит',
        tags: []
      }]
    };
  },
  mounted: function mounted() {
    this.getNodes();
  },
  watch: {
    notes: {
      handler: function handler(updatedList) {
        localStorage.setItem('notes', JSON.stringify(updatedList));
      },
      deep: true
    }
  },
  methods: {
    getNodes: function getNodes() {
      var localNotes = localStorage.getItem('notes');

      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },
    handleSubmit: function handleSubmit(value, activeTag) {
      var note = {
        title: value,
        tags: activeTag ? [activeTag] : []
      };
      this.notes.push(note);
    },
    handleRemove: function handleRemove(index) {
      this.notes.splice(index, 1);
    },
    editNote: function editNote(index) {
      this.editingInput.index = index;
      this.editingInput.value = this.notes[index];
    },
    onEdit: function onEdit(index) {
      this.editedNote.value = this.notes.splice(index, 1, this.editingInput.value);
      this.editingInput.index = null;
      this.editingInput.value = '';
    }
  }
};